.sale {
  background: #EFF2ED;
  height: 590px;
}

.sale__block {
  width: 570px;
  height: 430px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  background: url(../img/1.svg) 101% 0 no-repeat,
  url(../img/2.svg) 50% 100% no-repeat,
  url(../img/3.svg) 0 80% no-repeat,
  url(../img/image10.png) 26% 100% no-repeat #FFFFFF;
}

.sale__block1 {
  width: 570px;
  height: 200px;
  background: #FFFFFF;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;

  &--1 {
    margin-bottom: 30px;
    background: url(../img/4.svg) 0 0 no-repeat,
    url(../img/5.svg) 100% 0 no-repeat,
    url(../img/6.svg) 50% 100% no-repeat,
    url(../img/image11.png) 100% 92% no-repeat #FFFFFF;
  }

  &--2 {
    background: url(../img/7.svg) 10% 100% no-repeat,
    url(../img/8.svg) 30% 0 no-repeat,
    url(../img/9.svg) 70% 100% no-repeat,
    url(../img/10.svg) 100% 0 no-repeat #FFFFFF;
  }
}

.sale__block3 {
  padding: 30px 0 0 40px;
  width: 323px;
}

.sale__wrapper {
  width: 1170px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.sale__wrapper1 {
  padding-top: 80px;
}

.sale__block2 {
  padding: 40px 0 0 40px;
  width: 477px;
}

.sale__header {
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  color: #4F4F4F;
  margin: 0 0 25px 0;
  padding: 0;
}

.sale__description {
  margin: 0 0 25px 0;
  padding: 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #4F4F4F;
}

.sale__button {
  text-decoration: none;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #FFFFFF;
  background: #75C23D;
  box-shadow: 0 4px 4px rgba(131, 216, 70, 0.25);
  border-radius: 47px;
  padding: 12px 40px;
}

.sale__description2 {
  margin: 0 0 3px 0;
  padding: 0;
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  color: #4F4F4F;
}

.sale__header2 {
  margin: 0 0 30px 0;
  padding: 0;
  font-weight: 900;
  font-size: 30px;
  line-height: 36px;
  color: #75C23D;
}

.sale__button2 {
  text-decoration: none;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #FFD250;
  background: transparent;
  border: 1px solid #FFD250;
  border-radius: 47px;
  padding: 12px 40px;
}