.nav-third__list {
  display: flex;
  justify-content: space-between;
  width: 640px;
  padding: 0;
  margin: 32px 0 0 0;
}

.nav-third__item {
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-third__item a {
  text-decoration: none;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #FFFFFF;
  background: #FFD250;
  border-radius: 51px;
  padding: 5px 12px;
}

.nav-third__item a:hover {
  background: #66AA35;
}