.hit {
  margin-top: 80px;
}

.hit__wrapper {
  width: 1170px;
  margin: 0 auto;
}

.hit__header {
  margin: 0 0 30px;
  padding: 0;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  color: #4F4F4F;
}

.hit__button {
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #FFFFFF;
  background: #75C23D;
  border-radius: 46px;
  border: none;
  padding: 11px 62px 12px;
  margin-bottom: 30px;
}