.brand__button {
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #75C23D;
  position: relative;
  text-decoration: none;
}

.brand__button::before {
  position: absolute;
  content: " ";
  width: 2px;
  height: 8px;
  background: #75C23D;
  left: 125px;
  top: 3px;
  transform: rotate(-45deg);
}

.brand__button::after {
  position: absolute;
  content: " ";
  width: 2px;
  height: 8px;
  background: #75C23D;
  left: 125px;
  top: 8px;
  transform: rotate(45deg);
}