.nav-first__wrapper {
  border-bottom: 1px solid #F2F2F2;
}

.nav-first {
  display: flex;
  justify-content: space-between;
  width: 1170px;
  margin: 0 auto;
}

.nav-first__list {
  margin-top: 17px;
  margin-bottom: 17px;
  display: flex;
  justify-content: space-between;
  padding: 0;

  &--left {
    width: 467px;
  }

  &--right {
    width: 140px;
  }

}

.nav-first__item a {
  text-decoration: none;
  font-weight: normal;
  font-size: 12px;
  color: #4F4F4F;
}

.nav-first__item a:hover {
  color: #66AA35;
}

.nav-first__item {
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-first__line {
  position: relative;
}

.nav-first__line::before {
  position: absolute;
  content: " ";
  top: 1px;
  left: 50px;
  width: 1px;
  height: 12px;
  background-color: #75C23D;
}