.form {
  height: 1005px;
  margin-top: -119px;
  position: relative;
  background: url(../img/lapa1.svg) 4% 25% no-repeat,
  url(../img/lapa2.svg) 97% 95% no-repeat,
  url(../img/Vector3.svg) 95% 34% no-repeat,
  url(../img/Vector4.svg) 97% 42% no-repeat,
  url(../img/Vector5.svg) 90% 80% no-repeat,
  url(../img/Vector6.svg) 7% 88% no-repeat,
  url(../img/Shape.svg) 0 80% no-repeat,
  url(../img/Vector.svg) 0 45% no-repeat,
  url(../img/Vector2.svg) 4% 58% no-repeat #EFF2ED;
  z-index: 0;
}

.form-checkbox__window {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.form-checkbox__window+label {
  display: inline-flex;
  align-items: center;
  user-select: none;
}
.form-checkbox__window+label::before {
  content: '';
  display: inline-block;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  flex-grow: 0;
  border: 2px solid #F1E347;

  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}

.form-checkbox__window:checked+label::before {
  border-color: #75C23D;
  background-color: #F1E347;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

/* стили при наведении курсора на checkbox */
.form-checkbox__window:not(:disabled):not(:checked)+label:hover::before {
  border-color: #FFD250;
}
/* стили для активного состояния чекбокса (при нажатии на него) */
.form-checkbox__window:not(:disabled):active+label::before {
  background-color: #FFD250;
  border-color: #FFD250;
}
/* стили для чекбокса, находящегося в фокусе и не находящегося в состоянии checked */
.form-checkbox__window:focus:not(:checked)+label::before {
  border-color: #FFD250;
}
/* стили для чекбокса, находящегося в состоянии disabled */
.form-checkbox__window:disabled+label::before {
  background-color: #e9ecef;
}

.form__container {
  background: #FFFFFF;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  width: 1050px;
  height: 360px;
  margin: 0 auto;
  padding: 78px 60px 65px 63px;
}

.form__block1 {
  padding-top: 179px;
}

.form__block {
  display: flex;
  justify-content: space-between;
  width: 1015px;
  padding-bottom: 40px;
  margin-bottom: 20px;
  border-bottom: 1px solid #E0E0E0;
}

.form__wrapper {
  display: flex;
  margin-bottom: 40px;
}

.form__wrapper img {
  margin-right: 21px;
}

.form__header {
  margin: 0;
  padding: 0;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  color: #4F4F4F;
}

.form__list,
.form-checkbox {
  margin: 0;
  padding: 0;
  list-style: none;
}

.form__item-button {
  border: 1px solid #BDBDBD;
  box-sizing: border-box;
  border-radius: 47px;
  background-color: transparent;
  color: #BDBDBD;
  padding: 6px 39px;
  margin-bottom: 10px;

  &--1 {
    padding: 6px 38px;
  }

  &--2 {
    padding: 6px 40px;
  }

  &--3 {
    padding: 6px 35px;
  }

  &--4 {
    padding: 6px 43.5px;
  }
}

.form__item-button:hover {
  background: rgba(117,194,61,0.05);
  color: #75C23D;
  border: 1px solid #75C23D;
}

.form-checkbox__1 {
  height: 201px;
  width: 177px;
  margin-top: -5px;
  position: relative;

  &--3 {
    margin-right: 100px;
  }
}

.form-checkbox__1-header::before {
  position: absolute;
  content: " ";
  background: url(../img/Arrow.svg) no-repeat;
  width: 19px;
  height: 8px;
  top: 7px;
  right: 1px;
}

.form-checkbox {
  //overflow-y: scroll;
}

.form-checkbox__1-header {
  margin: 0 0 16px 0;
  padding: 0;
  font-weight: bold;
  font-size: 14px;
  line-height: 20px;
  color: #4F4F4F;
}

.form-checkbox__item label {
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  color: #4F4F4F;
}

.form-checkbox__item {
  margin-bottom: 9px;
}

.form__button {
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #FFFFFF;
  background: #75C23D;
  border-radius: 47px;
  padding: 12px 40px;
  float: right;
  margin-right: 30px;
  border: none;
}