.advantages {
  width: 1170px;
  margin: 0 auto;
  z-index: 1;
  position: relative;
}

.advantages__list {
  display: flex;
  justify-content: space-between;
  z-index: 100000;
  padding: 0;
  margin: 136px 0 0 0;
}

.advantages__item {
  display: flex;
  background: #FFFFFF;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 32px 22px 32px 25px;
  position: relative;
}

.advantages__wrapper--line::before {
  position: absolute;
  content: " ";
  top: 56px;
  right: -50px;
  width: 32px;
  height: 2px;
  background: #FFD250;
}

.advantages__image {
  margin-right: 20px;
}

.advantages__header {
  margin: 0 0 9px 0;
  padding: 0;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: #75C23D;
}

.advantages__description {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-size: 12px;
  line-height: 17px;
  color: #828282;
  width: 225px;
}