.menu, .menu__item {
  list-style: none;
  padding: 0;
  color: #fff;
  overflow: hidden;
}

.menu {
  position: absolute;
  top: 184px;
  bottom: auto;
  left: auto;
  display: block;
  width: 1170px;
  height: 503px;
  margin-left: -160px;
  padding: 0;
  line-height: 2em;
  background: #FFFFFF;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05);
  border-radius: 0px 0px 10px 10px;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  transform: translateY(-300%);
  transition: transform .5s;
  will-change: transform;
  backface-visibility: hidden;
  z-index: 2;
}

.menu__wrapper {
  display: flex;
}

.menu__list {
  margin: 0;
  padding: 25px 0 0 0;
  width: 229px;
  background: #FFF7E0;
  overflow: scroll;
  height: 503px;
}

.menu__top-container {
  display: flex;
  flex-direction: column;
}

.menu__top {
  margin-top: 30px;
  margin-left: 30px;
  display: flex;
  justify-content: space-between;
  width: 540px;
}

.menu__top1-list {
  margin: 0;
  padding: 0;
}

.menu__top1-item a {
  text-decoration: none;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #4F4F4F;
}

.menu__top1-item a:hover {
  color: #66AA35;
}

.menu__top1-item--arrow {
  position: relative;
}

.menu__top1-item--arrow::before {
  position: absolute;
  content: " ";
  top: 7px;
  left: 40px;
  width: 1px;
  height: 6px;
  background: black;
  transform: rotate(-45deg);
}

.menu__top1-item--arrow::after {
  position: absolute;
  content: " ";
  top: 11px;
  left: 40px;
  width: 1px;
  height: 6px;
  background: black;
  transform: rotate(45deg);
}

.menu__top1-item {
  margin-bottom: 5px;

  &--header {
    margin-bottom: 10px;
  }
}

.menu__top1-item--header a {
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  color: #FFD250;
}

.menu__item-wrapper {
  display: flex;
  text-decoration: none;
}

.menu__item-wrapper:hover {
  background-color: #FFFFFF;
}

.menu__item-wrapper img {
  margin: 7.5px 20px 5px 20px;
}

.menu__item-wrapper p {
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #4F4F4F;
  margin: 15px 0 10px 0;
}

.menu__item-wrapper--1 img {
  margin: 7.5px 15px 5px 15px;
}

.menu__item-wrapper--1 p {
  margin-top: 18px;
}

.menu__item-wrapper--2 img {
  margin: 7.5px 23px 5px 22px;
}

.menu__item-wrapper--3 img {
  margin: 7.5px 26px 5px 22px;
}

.menu__banner {
  background: #FFFFFF;
  border-radius: 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
  width: 284px;
  height: 448px;
  margin-top: 30px;
  margin-left: 56px;
  color: #4F4F4F;
  text-align: center;
  position: relative;
}

.menu__banner-gift {
  position: absolute;

  &--1 {
    top: 157px;
    left: 0;
  }

  &--2 {
    top: 375px;
    right: 0;
  }

  &--3 {
    top: 0;
    right: 97px;
  }
}

.menu__banner-description {
  padding-top: 68px;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  width: 235px;
  margin: 0 auto;
}

.menu__banner-header {
  margin-top: 10px;
  font-weight: 900;
  font-size: 30px;
  line-height: 36px;
  color: #75C23D;
}

.menu__banner-button {
  text-decoration: none;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #FFFFFF;
  background: #75C23D;
  border-radius: 47px;
  padding: 12px 40px;
  margin-top: 6px;
}