.description {
  width: 1170px;
  margin: 0 auto;
  height: 610px;
  display: flex;
  justify-content: space-between;
}

.description__item {
  width: 270px;
  height: 290px;
  background: #EFF2ED;
  border-radius: 5px;
}

.description__container {
  padding: 25px;
}

.description__block {
  display: flex;
  position: relative;
  margin-bottom: 19px;
}

.description__image {
  margin-right: 15px;
  z-index: 1;
}

.description__header {
  margin: 10px 0 0 0;
  padding: 0;
  font-weight: bold;
  font-size: 16px;
  line-height: 22px;
  color: #4F4F4F;
}

.description__list {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 570px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.description__item {
  margin-bottom: 30px;
}

.description__header::before {
  position: absolute;
  content: " ";
  background: #75C23D;
  width: 51px;
  height: 51px;
  border-radius: 50px;
  top: -8px;
  z-index: 0;
  left: -5px;
}

.description__text {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-size: 12px;
  line-height: 17px;
  color: #4F4F4F;
  height: 153px;
}

.description__button {
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #75C23D;
  position: relative;
}

.description__button::before {
  position: absolute;
  content: " ";
  width: 2px;
  height: 8px;
  background: #75C23D;
  left: 125px;
  top: 3px;
  transform: rotate(-45deg);
}

.description__button::after {
  position: absolute;
  content: " ";
  width: 2px;
  height: 8px;
  background: #75C23D;
  left: 125px;
  top: 8px;
  transform: rotate(45deg);
}

.description__button:hover {
  color: #FFD250;
  text-decoration: none;
}

.description__wrapper2 {
  background: url(../img/image12.png) 37% 302% no-repeat;
  height: 610px;
  width: 488px;
}

.description__header2 {
  margin: 0 0 24px 0;
  padding: 0;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  color: #4F4F4F;
  width: 346px;
}

.description__text2 {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-size: 12px;
  line-height: 17px;
  color: #4F4F4F;
}