.start {

}

.start__image {
  position: absolute;
  right: -350px;
  top: 1px;
  z-index: -1;
}

.start__svg {
  position: absolute;
  top: 102px;
  left: -20px;
  z-index: -1;
}

.start__container {
  width: 1170px;
  margin: 0 auto;
  position: relative;
  padding-left: 20px;
}

.start__header {
  margin: 0;
  padding: 137px 0 20px 0;
  font-weight: 900;
  font-size: 37px;
  line-height: 45px;
  color: #4F4F4F;
  width: 658px;
}

.start__description {
  margin: 0 0 40px 0;
  padding: 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #828282;
}

.start__button {
  text-decoration: none;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #FFFFFF;
  background: #75C23D;
  box-shadow: 0 4px 4px rgba(131, 216, 70, 0.25);
  border-radius: 47px;
  padding: 12px 40px;
}