.nav__check {
  display: none;
  text-transform: uppercase;
}

.nav {
  margin-top: 25px;
  user-select: none;
  background: #66AA35;
  border-radius: 51px;
}

.nav__showmenu {
  position: relative;
  z-index: 3;
  display: inline-block;
  margin: 14px 65px 0 22px;
  padding: 0;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #FFFFFF;
}

.nav__showmenu img {
  margin-left: 15px;
}

.nav__back {
  position: absolute;
  top: 184px;
  right: 0;
  bottom: 10px;
  left: 0;
  transition: background-color .3s;
}

.nav__check:checked~.nav__back {
  background-color: rgba(0,0,0,.2);
  bottom: 0;
  z-index: 2;
}

.menu__item:hover,.nav__showmenu:hover,.menu__showsub:hover {opacity: 0.7;}
.menu::-webkit-scrollbar {display: none;}
.nav__check:checked+.nav__showmenu {color:#fff;}
.menu__check:checked~.menu__submenu {max-height: 100vh;}
.menu__check:checked+.menu__showsub {transform: rotate(180deg);}
.nav__check:checked~.menu {transform: translateX(0);}


.nav__showmenu span {
  display: block;
  height: 2px;
  width: 20px;
  background-color: #FFFFFF;
  margin: 3px 0 0;
  transition: all .1s linear .23s;
  position: relative;
  top: -15px;
  left: 75px;
}

.nav__showmenu span.first {
  margin-top: 0;
}

.nav__showmenu span.second {
  width: 14px;
}

.nav__check:checked ~ .nav__showmenu {
  //top: 28px;
}

.nav__check:checked ~ .nav__showmenu span.first {
  transform: rotate(45deg);
  top: -10px;
  left: 75px;
}

.nav__check:checked ~ .nav__showmenu span.second {
  opacity: 0;
}

.nav__check:checked ~ .nav__showmenu span.third {
  transform: rotate(-45deg);
  top: -20px;
  left: 75px;
}