.stock {
  margin-top: -200px;
  position: relative;
  z-index: 1;
}

.stock__wrapper {
  width: 1170px;
  margin: 0 auto;
}

.stock__slider .slider .bx-wrapper .bx-viewport {
  height: 570px!important;
}

.stock__slider .slider--1 .bx-wrapper .bx-viewport {
  height: 75px!important;
}

.stock__slider .slider .bx-wrapper .bx-controls-direction a {
  top: -8%;
}

.stock__slider .slider--1 .bx-wrapper{
  height: 32px!important;
}

.stock__slider .slider--1 .bx-wrapper .bx-controls-direction a {
  top: -67%;
}

.stock__slider .slider .bx-wrapper .bx-prev {
  left: 1090px;
}

.stock__slider .slider .bx-wrapper {
  box-shadow: none;
  border: none;
  background: transparent;
}

.stock__slider .slider .bx-wrapper .bx-pager-item {
  display: none;
}

.stock__header {
  margin: 0 0 31px 0;
  padding: 0;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  color: #4F4F4F;
}

.slider__item {
  background: #FFFFFF;
  box-shadow: 0 0 10px rgba(117, 194, 61, 0.2);
  border-radius: 10px;
  width: 270px!important;
  margin-right: 30px;

  &--1 {
    border: 1px solid #BDBDBD;
    box-sizing: border-box;
    border-radius: 5px;
    width: 184px!important;
    box-shadow: none;
    height: 54px;
    margin-right: 50px;
  }
}

.stock__container {
  padding-left: 30px;
  margin-top: 51px;
}

.stock__slider-image {
  margin: 38px 47px 0 48px;

  &--1 {
    margin-left: 70px;
  }
}

.stock__image-wrapper {
  position: relative;
}

.stock__image1 {
  position: absolute;
  top: -10px;
  left: 30px;
  font-weight: bold;
  font-size: 10px;
  line-height: 12px;
  color: #FFD250;
  border: 1px solid #FFD250;
  box-sizing: border-box;
  border-radius: 30px;
  padding: 11px 5px;

  &--1 {
    color: #177695;
    border: 1px solid #177695;
    top: 70px;
    padding: 9px 6px;
  }

  &--2 {
    color: #177695;
    border: 1px solid #177695;
    top: -10px;
    padding: 9px 6px;
  }

  &--3 {
    top: 30px;
  }

  &--4 {
    color: #83D846;
    border: 1px solid #83D846;
  }
}

.stock__image2 {
  position: absolute;
  top: 30px;
  left: 30px;
  font-weight: bold;
  font-size: 10px;
  line-height: 12px;
  color: #75C23D;
  border: 1px solid #75C23D;
  box-sizing: border-box;
  border-radius: 30px;
  padding: 10px 6px;

  &--1 {
    top: 70px;
  }

  &--2 {
    color: #FFD250;
    border: 1px solid #FFD250;
  }
}

.stock__image3 {
  position: absolute;
  top: 160px;
  left: 215px;
  font-weight: bold;
  font-size: 10px;
  line-height: 12px;
  color: #FFFFFF;
  background: #75C23D;
  box-sizing: border-box;
  border-radius: 30px;
  padding: 11px 6px;
}

.stock__image4 {
  position: absolute;
  top: -10px;
  left: 223px;
}

.stock__slider-header {
  margin: 0;
  padding: 0;
  font-weight: bold;
  font-size: 14px;
  line-height: 20px;
  color: #4F4F4F;
}

.stock__slider-description {
  padding: 0;
  margin: 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #4F4F4F;
  width: 214px;
}

.stock__image-block1 {
  width: 211px;
  border: 1px solid #E0E0E0;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 6px;
}

.stock__image-block3 {
  display: flex;

  &--1 {
    margin-top: 98px;
  }

  &--2 {
    margin-top: 58px;
  }

  &--3 {
    margin-top: 87px;
  }

  &--4 {
    margin-top: 53px;
  }
}

.stock__cost {
  margin: 0 15px 10px 0;
  padding: 0;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  color: #4F4F4F;
}

.stock__cost1 {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-size: 20px;
  line-height: 24px;
  text-decoration-line: line-through;
  color: #BDBDBD;
}

.stock__button1 {
  border: 1px solid #75C23D;
  box-sizing: border-box;
  border-radius: 46px;
  padding: 5px 50px;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #4F4F4F;
  display: flex;
  margin: 0 0 10px 0;
  width: 211px;
}

.stock__button2 {
  background-color: transparent;
  border: none;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  color: #75C23D;
  margin-right: 31px;

  &--1 {
    margin-right: 0;
    margin-left: 29px;
  }
}

.stock__button3 {
  margin: 5px 0 0 0;
  padding: 0;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #4F4F4F;
}

.stock__button5 {
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #75C23D;
  border: 1px solid #75C23D;
  box-sizing: border-box;
  border-radius: 46px;
  background-color: transparent;
  padding: 11px 47px 12px 46px;
  margin-bottom: 30px;
}

.stock__button5:hover {
  background-color: #66AA35;
  color: #FFFFFF;
}

.stock__button5:active {
  background-color: #4B7D27;
  color: #FFFFFF;
}

.stock__button6 {
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #FFFFFF;
  background: #75C23D;
  border-radius: 46px;
  border: none;
  padding: 11px 66px 12px;
  margin-bottom: 10px;
}

.stock__button6:hover {
  border: 1px solid #75C23D;
  background-color: #FFFFFF;
  color: #66AA35;
}

.stock__button6:active {
  border: 1px solid #75C23D;
  background-color: #FFFFFF;
  color: #4B7D27;
}