.search {
  margin-top: 24px;
}

.search__window {
  border: 1px solid #BDBDBD;
  box-sizing: border-box;
  border-radius: 51px;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  color: black;
  padding: 12px 40px 13px 21px;
}

.search__button {
  background-color: transparent;
  border: none;
  position: absolute;
  right: 10px;
  top: 12px;
}

.search__form {
  position: relative;
}