.page-footer {
  margin-top: 80px;
  background: #E6ECEE;
}

.page-footer__container1 {
  border-bottom: 1px solid #BDBDBD;
}

.page-footer__wrapper {
  width: 1170px;
  margin: 0 auto;
  padding-top: 72px;
  padding-bottom: 84px;
  display: flex;
  justify-content: space-between;
}

.page-footer__block {
  padding-top: 7px;
  display: flex;
  justify-content: space-between;
  width: 500px;
}

.page-footer__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.page-footer__item--1 {
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  color: #4F4F4F;
}

.page-footer__item {
  padding: 0;
  margin: 0 0 10px 0;

  &--1 {
    margin-bottom: 30px;
  }
}

.page-footer__item a {
  text-decoration: none;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #4F4F4F;
}

.page-footer__wrapper1 {
  padding-top: 8px;
}

.page-footer__item a:hover {
  color: #75C23D;
}

.page-footer__block1 {
  display: flex;
  margin-bottom: 23px;
}

.page-footer__block1 img {
  margin-right: 15px;
}

.page-footer__description {
  margin: 2px 0 0 0;
  padding: 0;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  color: #4F4F4F;
}

.page-footer__block2 {
  display: flex;
  flex-direction: column;
}

.page-footer__phone {
  text-decoration: none;
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
  color: #75C23D;
}

.page-footer__container {
  margin: 0 auto;
  width: 1170px;
  display: flex;
  justify-content: space-between;
}

.page-footer__text {
  padding: 18px 0 20px 0;
  margin: 0;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  color: #4F4F4F;
}